.gift-heading {
padding: 81px 0 10px;
    p{
        font-family: hl;
    }
.gift-form {
    padding-top: 67px;
    .form-group {
        max-width: 79%;
    }
    .form-control {
        padding: 6px 69px !important;
    }
    .btn-search {
        position: absolute;
        top: 0;
        width: 57px;
        height: 100%;
        left: 0;
        background: $g-cl;
        border: none;
        padding: 15px 0;
        @include maxms {
            padding: 7px 0;
        }
        .icon-search {
            background-image: url('data:image/png;base64,');
            background-size: contain;
            background-position: center;
            width: 28px;
            height: 28px;
            display: inline-block;
        }
    }

    .search-bar {
        position: relative;
        clear: both;
        @include maxsm{
            margin-bottom: 10px;
        }
    }
}

}

.gift-heading {
    h1 {
        margin-bottom: 10px;
    }
}

.procoins-gift {
    padding: 0 !important;
    margin-bottom: 70px;
    .progress-tab{
        @include maxsm{
            margin-bottom: 40px;
        }
    }
    .gift-table {
        position: relative;
        margin-bottom: 0;
        min-height: 387px;
        @include maxsm{
            min-height: 300px;
        }
        .table {
            th {
                width: 33.33%;
                text-align: center;
                font-family: hn-b;
                font-size: 20px;
                color: $cl-complete;
                border: 1px solid $cl-complete;
                border-top: 1px solid $cl-complete !important;
                padding: 28px 15px;
                vertical-align: middle;
                @include maxsm{
                    font-size: 16px;
                    padding: 12px 15px;
                }
                @include maxms{
                    padding: 10px;
                    font-size: 14px;
                }
            }
            tr {
                td {
                    padding: 10px 15px;
                    @include maxsm{
                        padding: 6px 10px;
                    }
                }
            }
        }
        .no-data {
            position: absolute;
            color: $r-cl;
            width: 100%;
            bottom: 0;
            height: 77.5%;
            margin: 0 auto;
            text-align: center;
            background: #fff;
            @include maxsm{
                height: 83.4%;
            }
            i {
                font-size: 70px;
                @include maxsm{
                    font-size: 50px;
                }
            }
            p {
                font-size: 21px;
                font-family: hn-b;
                color: $r-cl;
                margin-top: 13px;
                @include maxsm{
                    font-size: 17px;
                }
            }
            .data-content {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
            }
        }
    }
    .sec-bttm {
        height: 72px;
        border-top: 1px solid $bg-offwhite;
        background: $white;
        @include maxsm{
            height: 50px;
        }
    }
}
