body {
  font-family: 'h';
  font-size: 14px;
  color: #000;
}

.dt {
  display: table;
  height: 100%;
  width: 100%;
}

.dtc {
  display: table-cell;
  height: 100%;
  width: 100%;
  vertical-align: middle;
}

@media (max-width: 767px) {
  .table-responsive {
    border: none;
  }
}

@media (min-width: 1200px) {
  .container {
    width: 1170px;
  }
}

@media (min-width: 1270px) {
  .container {
    width: 1230px;
  }
}

nav {
  background-color: #161c22;
}

nav .logo {
  padding: 12px 0;
}

@media (max-width: 767px) {
  nav .logo {
    padding: 12px 0;
    width: 85px;
  }
  nav .logo img {
    max-width: 74%;
  }
}

@media (max-width: 767px) {
  nav .menu-toggle {
    padding: 10px 0;
  }
}

@media (max-width: 767px) {
  nav > .container {
    position: relative;
  }
}

.menu-toggle {
  color: #fff;
  font-size: 28px;
}

.main-menu {
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.8);
  display: block;
  visibility: hidden;
  opacity: 0;
  transition: all 0.5s ease-out;
  transform: translateY(-100%);
}

.main-menu.bg-light {
  background-color: rgba(255, 255, 255, 0.8);
}

.main-menu.active {
  transform: translateY(0);
  visibility: visible;
  opacity: 1;
}

.main-menu .menu-container {
  display: table;
  width: 100%;
  height: 100%;
  text-align: center;
  position: relative;
}

.main-menu ul {
  display: table-cell;
  vertical-align: middle;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.main-menu ul li {
  display: block;
}

.main-menu ul a {
  color: #27a6b6;
  font-size: 43px;
  font-family: 'ab';
  text-decoration: none;
  line-height: 85px;
  text-transform: capitalize;
}

.main-menu ul a:focus, .main-menu ul a:active, .main-menu ul a:hover {
  color: #fd604b;
}

.main-menu ul a.active {
  color: #fd604b;
  font-weight: bold;
}

@media (max-width: 479px) {
  .main-menu ul a {
    font-size: 18px;
    line-height: 3;
  }
}

@media (max-width: 767px) {
  .main-menu ul a {
    font-size: 22px;
    line-height: 2;
  }
}

.main-menu .menu-rocket {
  position: absolute;
  transition: all 0.5s;
}

@media (max-width: 767px) {
  .main-menu .menu-rocket {
    max-width: 100px;
  }
}

.main-menu .menu-close {
  font-size: 40px;
  position: absolute;
  right: 50px;
  top: 50px;
  text-decoration: none;
  color: #565b5f;
}

@media (max-width: 479px) {
  .main-menu .menu-close {
    top: 20px;
    right: 20px;
  }
}

.nav-bar {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.nav-bar li > a {
  text-decoration: none;
  color: #fff;
  font-size: 30px;
  position: relative;
  padding: 27px 0 27px 25px;
  display: block;
}

.nav-bar li > a:hover {
  color: #ff5f44;
}

@media (max-width: 767px) {
  .nav-bar li > a {
    font-size: 18px;
    padding: 8px 0;
  }
}

.nav-bar li {
  float: left;
}

@media (max-width: 767px) {
  .nav-bar li {
    padding-left: 10px;
  }
  .nav-bar li:first-child {
    padding-left: 0;
  }
}

.nav-bar li.n-coin > a {
  font-family: 'hb';
  font-size: 23px;
  width: 170px;
  display: block;
}

@media (max-width: 767px) {
  .nav-bar li.n-coin > a {
    font-size: 14px;
    width: auto;
    display: block;
    white-space: nowrap;
    overflow: hidden;
    line-height: 22px;
  }
}

.nav-bar li.n-coin span {
  margin-right: 5px;
  display: inline-block;
}

@media (max-width: 767px) {
  .nav-bar li.n-coin span {
    float: left;
    width: 15px;
    height: 15px;
    margin-top: 3px;
  }
}

.nav-bar li.n-coin .submenu {
  z-index: 99;
}

.nav-bar li.n-user .submenu {
  z-index: 999;
}

.nav-bar li.n-user > a {
  display: block;
  margin-top: -5px;
  font-size: 40px;
}

@media (max-width: 767px) {
  .nav-bar li.n-user > a {
    font-size: 27px;
    margin-top: -3px;
  }
}

.nav-bar li.n-user > a .badge {
  position: absolute;
  top: 16px;
  right: -8px;
  height: 20px;
  width: 20px;
  display: block;
  background-color: #fd604b;
  border-radius: 50%;
  color: #fff;
  text-align: center;
  font-family: 'hn-b';
  font-size: 13px;
  padding: 0;
  line-height: 20px;
}

@media (max-width: 767px) {
  .nav-bar li.n-user > a .badge {
    top: 0;
  }
}

.coins > a,
.n-user > a,
.n-coin > a,
.n-window > a,
.n-menu > a {
  line-height: 30px;
}

.coins {
  display: inline-block;
  width: 20px;
  height: 20px;
}

.submenu-container {
  position: relative;
}

@media (max-width: 767px) {
  .submenu-container {
    position: static;
  }
}

.submenu-container .submenu {
  width: 400px;
  background-color: rgba(22, 28, 34, 0.7);
  padding: 20px;
  position: absolute;
  visibility: hidden;
  top: 100%;
  right: 0;
  opacity: 0;
  transition: all 0.5s ease-out;
}

@media (max-width: 767px) {
  .submenu-container .submenu {
    width: 100%;
    text-align: center;
  }
}

.submenu-container .submenu:before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  width: 0;
  height: 0;
  border-left: 25px solid transparent;
  border-right: 25px solid transparent;
  border-top: 20px solid rgba(22, 28, 34, 0.7);
}

@media (max-width: 767px) {
  .submenu-container .submenu:before {
    left: 50%;
    transform: translateX(-50%);
  }
}

.submenu-container.active .submenu {
  opacity: 1;
  visibility: visible;
}

.submenu-container .btns {
  text-align: center;
  padding-top: 15px;
}

.submenu-container .btns .btn {
  font-weight: 900;
}

.submenu-container h2 {
  color: #fff;
  padding: 0;
  margin: 0;
  font-size: 33px;
  font-family: 'am';
  display: block;
}

.submenu-container p {
  color: #fff;
  padding: 0;
  margin: 0;
  font-family: 'ht';
  display: block;
  font-size: 14px;
}

.submenu-container .user-avatar {
  width: 100px;
  height: 100px;
  overflow: hidden;
  border-radius: 50%;
  display: inline-block;
  vertical-align: middle;
}

.submenu-container .user-avatar img {
  width: 100%;
  height: 100%;
}

.submenu-container .user-name {
  display: inline-block;
  vertical-align: middle;
  width: 70%;
}

.submenu-container .vol-btn {
  margin-top: 20px;
  position: absolute;
  top: 0;
  right: 15px;
}

.submenu-container .vol-btn span {
  cursor: pointer;
}

.submenu-container.n-coin .submenu {
  width: 260px;
}

@media (max-width: 767px) {
  .submenu-container.n-coin .submenu {
    width: 100%;
  }
}

.submenu-container.n-coin h2 {
  padding-top: 30px;
}

.submenu-container.n-coin .btns {
  text-align: left;
}

@media (max-width: 767px) {
  .submenu-container.n-coin .btns {
    text-align: center;
  }
}

.user-snapshot {
  display: block;
  margin-top: 20px;
}

