  }
}

@media (max-width: 991px) {
  .custom-tab-container .custom-tab a {
    font-size: 18px;
  }
}

@media (max-width: 767px) {
  .custom-tab-container .custom-tab a {
    font-size: 15px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    padding: 15px 10px;
    height: 100%;
  }
}

@media (max-width: 479px) {
  .custom-tab-container .custom-tab a {
    font-size: 13px;
  }
}

.custom-tab-container .custom-tab a > span {
  position: relative;
  z-index: 99;
  transition: background-color 0.3s ease-out;
}

.custom-tab-container .custom-tab a:after, .custom-tab-container .custom-tab a:before {
  position: absolute;
  content: '';
  top: 0;
  display: block;
  width: 50px;
  height: 110px;
  transition: background-color 0.3s ease-out;
}

@media (max-width: 767px) {
  .custom-tab-container .custom-tab a:after, .custom-tab-container .custom-tab a:before {
    display: none;
  }
}

.custom-tab-container .custom-tab a:before {
  left: 0;
  transform: rotate(22deg);
}

.custom-tab-container .custom-tab a:after {
  right: 0;
  transform: rotate(-22deg);
}

.custom-tab-container .custom-tab:first-child {
  padding-left: 0;
}

.custom-tab-container .custom-tab:first-child a:before {
  display: none !important;
}

.custom-tab-container .custom-tab:last-child {
  padding-right: 0;
}

.custom-tab-container .custom-tab:last-child a:after {
  display: none !important;
}

