.btn-default:not(.disabled) {
  background: #161c22;
  font-family: hb;
  text-transform: uppercase;
  color: #fff;
  font-size: 17px;
  padding: 7px 52px;
  display: inline-block;
  border-color: #161c22;
  margin-bottom: 23px;
  line-height: 1.5;
  position: relative;
}

@media (max-width: 1199px) {
  .btn-default:not(.disabled) {
    padding: 7px 43px;
  }
}

@media (max-width: 991px) {
  .btn-default:not(.disabled) {
    padding: 7px 38px;
  }
}

@media (max-width: 767px) {
  .btn-default:not(.disabled) {
    padding: 7px 52px;
  }
}

@media (max-width: 479px) {
  .btn-default:not(.disabled) {
    font-size: 14px;
  }
}

.btn-default:not(.disabled):hover {
  background: transparent;
  color: #161c22;
  border-color: #161c22;
}

.btn-default:not(.disabled):before {
  content: '';
  position: absolute;
  right: 10px;
  top: 50%;
  margin-top: -11px;
  width: 20px;
  height: 20px;
  border: 4px solid;
  border-left-color: transparent;
  border-radius: 50%;
  opacity: 0;
  transition-duration: 0.2s;
  transition-property: opacity;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-name: rotate;
  animation-timing-function: linear;
}

.btn-default:not(.disabled):after {
  content: '';
  display: inline-block;
  height: 100%;
  width: 0px;
  transition-delay: 0.3s;
  transition-duration: 0.75s;
  transition-property: width;
}

.btn-default:not(.disabled).sending {
  pointer-events: none;
  cursor: not-allowed;
}

.btn-default:not(.disabled).sending:before {
  transition-delay: 0.5s;
  transition-duration: 1s;
  opacity: 1;
}

.btn-default:not(.disabled).sending:after {
  transition-delay: 0s;
  width: 20px;
}

